import React from "react";
import $ from "jquery"
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

var active = 0;

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    for (let i = 0; i < 5; i++) {
      $(".date-btn").eq(i).on("click", () => {
        this.changeMap(i);
      });
    }
  }

  changeMap(index) {
    $(".date-btn").eq(active).attr("class", "date-btn");
    $(".date-btn").eq(index).attr("class", "date-btn selected");

    active = index;
  }

  render() {    
    return (
      <div>
        
        <h1>New York</h1>

        <Row>
          <Col xs={3}>

            <div className="date-btn selected">2020-01-01</div>

            <div className="date-btn">2020-04-01</div>

            <div className="date-btn">2020-07-01</div>

            <div className="date-btn">2020-10-07</div>

            <div className="date-btn">2021-01-06</div>

          </Col>

          <Col>
            <img
              id="legend_1"
              src="https://eodashboard.org/data/trilateral/WaterQuality_legend_trilateral_tsm.png"
            />
            <img
              id="legend_2"
              src="https://eodashboard.org/data/trilateral/WaterQuality_legend_trilateral.png"
            />

            <div align="center">Map goes here</div>
          </Col>
        </Row>
      </div>
    );
  }
}
